import { Component, h, Prop } from '@stencil/core';

/**
 * @category Form Controls
 * @description A form label component that provides accessible labeling with optional required indicators and info tooltips. Designed for use with form inputs.
 */
@Component({
    tag: 'atui-form-label',
    shadow: false,
})
export class AtuiFormLabelComponent {
    /**
     * Label that appears before the info icon.
     */
    @Prop()
    label: string;

    /**
     * When true, there will be a red star on the label.
     */
    @Prop()
    required: boolean;

    /**
     * The text to be contained in the tooltip.
     */
    @Prop()
    info_text: string;

    /**
     * Placed in the 'for' attribute on the label element
     */
    @Prop()
    for: string;

    render() {
        return (
            <div class="flex items-center gap-8">
                {[
                    (this.label || this.required) && (
                        <label
                            htmlFor={this.for ?? undefined}
                            class="flex gap-4"
                        >
                            {this.label}
                            {this.required && <span class="text-error">*</span>}
                        </label>
                    ),
                    this.info_text && (
                        <atui-tooltip position="right">
                            <span
                                slot="tooltip-trigger"
                                class="material-icons !text-icon-sm text-light cursor-pointer"
                            >
                                info_outline
                            </span>
                            <span>{this.info_text}</span>
                        </atui-tooltip>
                    ),
                ]}
            </div>
        );
    }
}
